

































































































































































import { ApiService } from "@/x-vue/services/api.service";
import { UserModel } from "@/x-vue/services/interfaces";
import Vue from "vue";
import Component from "vue-class-component";

@Component({})
export default class AdminUserList extends Vue {
  users: Array<UserModel> = [];
  total = 0;

  limit = 3;
  searchKey = "";
  noOfPages = 10;
  currentPage = "1";

  options = {
    email: true,
    firebaseUid: false,
    name: true,
    nickname: true,
    point: true,
    phoneNo: true,
    gender: false,
    birthdate: false,
    countryCode: false,
    province: false,
    city: false,
    address: false,
    zipcode: false,
    createdAt: false,
    updatedAt: false,
  };

  linkGen(pageNum: number): string {
    return pageNum === 1 ? "?" : `?page=${pageNum}`;
  }

  onPageChanged(page: number): void {
    console.log("page; ", page);
    this.onSubmitSearch();
  }

  mounted(): void {
    this.currentPage = this.$route.query.page as string;
    this.onSubmitSearch();
  }

  async onSubmitSearch(): Promise<void> {
    console.log("page changed", this.currentPage);
    try {
      this.users = await ApiService.instance.userSearch({
        searchKey: this.searchKey,
        limit: this.limit,
        page: this.currentPage ?? "1",
        full: true,
      });
      console.log(this.users);

      this.total = await ApiService.instance.userCount({
        searchKey: this.searchKey,
      });
      this.noOfPages = Math.ceil(this.total / this.limit);
    } catch (e) {
      ApiService.instance.error(e);
    }
  }
}
