






























import Vue from "vue";
import Component from "vue-class-component";
import AdminUserList from "@/x-vue/components/admin/UserList.vue";
import AdminCategoryList from "@/x-vue/components/admin/CategoryList.vue";
import AdminPostList from "@/x-vue/components/admin/PostList.vue";
import AdminFileList from "@/x-vue/components/admin/FileList.vue";
import AdminSettings from "@/x-vue/components/admin/AdminSettings.vue";
import AdminPushNotification from "@/x-vue/components/admin/PushNotification.vue";
@Component({
  components: {
    AdminUserList,
    AdminCategoryList,
    AdminPostList,
    AdminFileList,
    AdminSettings,
    AdminPushNotification,
  },
})
export default class Admin extends Vue {
  activePage = "user";

  mounted(): void {
    if (this.$route.params.page) {
      this.activePage = this.$route.params.page;
    }
  }
}
